<template>
    <l-control position="bottomright">
        <v-card class="info-control total">
            <v-row align="center" justify="center" no-gutters>
                <v-col cols="12" class="pt-3">
                    <v-btn v-if="isdetailed" class="ma-0 pa-0" fab dark small color="primary" absolute top right @click="onMapSummaryCloseBtn">
                        <v-icon dark>mdi-close</v-icon>
                    </v-btn>
                    <h3 class="text-center" id="title">{{title}}</h3>
                </v-col>
                <v-col cols="12" class="primary lighten-1 py-2">
                    <h1 class="text-center attribute-name py-1">{{total}}</h1>
                    <h5 class="text-center">Total</h5>
                </v-col>
                <v-col cols="12" class="py-2">
                    <h2 class="text-center attribute-name py-1">{{positive}}</h2>
                    <h5 class="text-center">Positive</h5>
                </v-col>
                <v-col cols="12" class="primary lighten-3 py-2">
                    <h2 class="text-center attribute-name py-1">{{recovered}}</h2>
                    <h5 class="text-center">Recovered</h5>
                </v-col>
                <v-col cols="12" class="py-2">
                    <h2 class="text-center attribute-name py-1">{{deaths}}</h2>
                    <h5 class="text-center">Deaths</h5>
                </v-col>
                <v-col cols="12" class="primary lighten-3 py-2">
                    <v-row no-gutters>
                        <v-col cols="6">
                            <h3 class="text-center attribute-name py-1">{{pui_negative}}</h3>
                            <h6 class="text-center">Negative</h6>
                        </v-col>
                        <v-col cols="6">
                            <h3 class="text-center attribute-name py-1">{{pui_unresolved}}</h3>
                            <h6 class="text-center">Unresolved</h6>
                        </v-col>
                    </v-row>
                    <v-row no-gutters>
                        <v-col>
                            <h5 class="text-center">PUIs</h5>
                        </v-col>
                    </v-row>
                </v-col>
            </v-row>
        </v-card>

    </l-control>
</template>

<script>
    import {LControl} from 'vue2-leaflet'

    export default {
        name: "MapSummaryControl",
        props: {
            summary: Object
        },
        components: {
            LControl
        },
        data() {
            return {
                isdetailed: false,
            }
        },
        computed: {
             title() {
                return this.summary.title
            },
            total() {
                return this.summary.total
            },
            positive() {
                return this.summary.covid_positive
            },
            recovered() {
                return this.summary.covid_recovered
            },
            deaths() {
                return this.summary.covid_deaths
            },
            pui_negative() {
                return this.summary.pui_negative
            },
            pui_unresolved() {
                return this.summary.pui_unresolved
            }
        },
        methods: {
            onMapSummaryCloseBtn() {
                this.$emit('closebtn')
                this.isdetailed = false
            }
        },
        watch: {
            title(n, o) {
                this.isdetailed = this.title !== 'Western Visayas'
            }
        }
    }
</script>

<style scoped>
    .info-control.total {
        width: 270px;
    }

    .total .attribute-name.infections {
        color: #0000FF;
    }

    .total .attribute-name.recoveries {
        color: #00FF00;
    }

    .total .attribute-name.deaths {
        color: #FF0000;
    }

    .total .attribute-name.pui,
    .total .attribute-name.pum {
        color: #b0b000;
    }

    #title {
        line-height: 30px;
    }

</style>