<template>
    <l-map id="map"
           :center="center"
           :zoom.sync="zoom"
           :options="{zoomControl: false, preferCanvas: true, doubleClickZoom: false}" >
        <l-control position="topleft" class="info-control mode_selection py-4">
            <h3 class="text-center">CoViD-19 Data</h3>
            <h4 class="text-center">{{asof}}</h4>
            <v-overflow-btn
                    :items="attributes"
                    label="Overflow Btn"
                    return-object
                    v-model="attributeSelected"
            ></v-overflow-btn>
        </l-control>
        <l-choropleth-layer v-if="choropleth_loaded" :geojson="geojson" :data="response_data" :value="choroplethValue"
                            titleKey="citymun"
                            geojsonIdKey="citymunCode"
                            idKey="citymunCode"
                            :colorScale="colorScale"
                            min="0"
                            :currentItem="currentItem"
                            v-on:currentItemClick="onCurrentItemClick">
            <template slot-scope="props">
                <map-summary-control v-if="choropleth_loaded"
                                     :summary="mapSummaryData"
                                     v-on:closebtn="onMapSummaryCloseBtn"
                ></map-summary-control>
                <l-reference-chart
                        id="reference-chart"
                        :title="infoTitle"
                        :colorScale="colorScale"
                        min="0"
                        :max="getMax(props.max)"
                        position="topright"/>
            </template>
        </l-choropleth-layer>
        <v-overlay :value="!choropleth_loaded">
            <v-progress-circular indeterminate size="64"></v-progress-circular>
        </v-overlay>
    </l-map>

</template>

<script>
    import {LMap, LTileLayer, LMarker, LPopup, LGeoJson, LControl, LControlZoom} from "vue2-leaflet"
    import L from 'leaflet'
    import 'leaflet.icon.glyph'
    import geojson from '../assets/regionvi_citymun_lite'
    import ReferenceChart from '../components/ReferenceChart'
    import ChoroplethLayer from '../components/ChoroplethLayer'
    import MapSummaryControl from "../components/MapSummaryControl"
    import {getPUIMap} from '../util/api'


    export default {
        name: "Map",
        components: {
            LMap, LTileLayer, LMarker, LPopup, LGeoJson, LControl, LControlZoom,
            'l-choropleth-layer': ChoroplethLayer,
            'l-reference-chart': ReferenceChart,
            MapSummaryControl
        },
        created() {
            getPUIMap().then(res => {
                const data = res.data
                this.response_data = data.data
                this.response_total = data.total
                this.asof = data.date
                this.mapSummaryData = {
                    title: 'Western Visayas',
                    ...this.response_total
                }

                this.choropleth_loaded = true
            })
        },
        computed: {
            /*icon() {
                let base = {
                    className: 'custom-icons',
                    prefix: '',
                    glyph: '2'
                }

                return L.icon.glyph({
                    ...base,
                    glyphSize: '24px',
                    glyphAnchor: [0, 0],
                    glyphColor: 'blue',
                    iconUrl: require('../assets/marker.png'),
                    //iconUrl: '',
                    iconSize: [42, 53],
                    iconAnchor: [21, 53],
                    popupAnchor: [0, -64]
                })

            },*/
            geojson() {
                return geojson
            },
            colorScale() {
                switch (this.attributeSelected.value) {
                    case 'covid_positive':
                        return ["#ddd", "#0000FF", "#00009b"]
                    case 'covid_recovered':
                        return ["#ddd", "#00FF00", "#009b00"]
                    case 'covid_deaths':
                        return ["#ddd", "#FF0000", "#9B0000"]
                    case 'pui_unresolved':
                        return ["#ddd", "#FFFF00", "#A7A700"]
                    case 'pui_negative':
                        return ["#ddd", "#FF9700", "#9B5C00"]
                    default:
                        return ["#ffffff", "#999999", "#000000"]
                }
            },
            choroplethValue() {
                switch (this.attributeSelected.value) {
                    case 'covid_positive':
                        return {key: "covid_positive", metric: "CoVid19 Positive"}
                    case 'covid_recovered':
                        return {key: "covid_recovered", metric: "Recovered"}
                    case 'covid_deaths':
                        return {key: "covid_deaths", metric: "Deaths"}
                    case 'pui_unresolved':
                        return {key: "pui_unresolved", metric: "PUIs Unresolved"}
                    case 'pui_negative':
                        return {key: "pui_negative", metric: "PUIs Negative"}
                    default:
                        return {key: "", metric: ""}
                }
            },
            infoTitle() {
                switch (this.attributeSelected.value) {
                    case 'covid_positive':
                        return 'CoViD19 Positive'
                    case 'covid_recovered':
                        return 'Recovered'
                    case 'covid_deaths':
                        return ' Deaths'
                    case 'pui_unresolved':
                        return 'PUIs Unresolved'
                    case 'pui_negative':
                        return 'PUIs Negative'
                    default:
                        return ''
                }
            },
        },
        data() {
            return {
                center: [10.712317, 122.562362],
                zoom: 8,
                url: 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png',
                attribution: 'leaflet',
                attributes: [
                    {text: 'COVID-19 Positive', value: 'covid_positive'},
                    {text: 'Recovered', value: 'covid_recovered'},
                    {text: 'Deaths', value: 'covid_deaths'},
                    {text: 'PUIs Negative', value: 'pui_negative'},
                    {text: 'PUIs Unresolved', value: 'pui_unresolved'},
                ],
                attributeSelected: {text: 'COVID-19 Positive', value: 'covid_positive'},
                choropleth_loaded: false,
                summary_loaded: false,
                currentItem: { name: "", value: 0, key: ""},
                response_data: [],
                response_total: {},
                asof: '',
                mapSummaryData: {
                    title: 'Western Visayas',
                    ...this.response_total
                }

            }
        },
        methods: {
            getMax(i) {
                if (i === 0) {
                    return 2
                } else {
                    return i & 1 ? i + 1 : i
                }
            },
            onCurrentItemClick(e) {
                if (e.key !== "") {
                    const item = this.response_data.find(x => x['citymunCode'] === e.key)
                    if (item) {
                        this.mapSummaryData = {
                            title: e.name,
                            ...item
                        }
                    }
                } else {
                    this.onMapSummaryCloseBtn()
                }
            },
            onMapSummaryCloseBtn() {
                this.mapSummaryData = {
                    title: 'Western Visayas',
                    ...this.response_total
                }
            }
        },
        watch: {
            zoom(n, o) {
                console.log(`watch zoom ${o} to ${n}`)
            },
            attributeSelected(n, o) {
                this.choropleth_loaded = false
                this.$nextTick(() => {
                    this.onMapSummaryCloseBtn()
                    this.choropleth_loaded = true
                })
            },
            watch: {

            }
        }
    }
</script>

<style>
    #map {
        z-index: 1;
        height: calc(100vh - 92px);
        width: 100%;
    }

    .info-control {
        padding: 6px 8px;
        font: 18px/20px sans-serif;
        background: white;
        background: rgba(255, 255, 255, 0.8);
        box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
        border-radius: 5px;
    }

    .info-control h4 {
        margin: 0 0 5px;
        color: #777;
    }

    .info-control .gradient {
        width: 180px;
    }

    .info-control.mode_selection {
        width: 220px;
    }



</style>